import Avatar from "../../components/Avatar";
import { useAuth } from "../../contexts/AuthContext";

function DropdownToggle({ onClick }) {
  const {
    user: { profileImage }
  } = useAuth();
  return (
    <div onClick={onClick}>
      <Avatar src={profileImage} size="50" />
    </div>
  );
}

export default DropdownToggle;
